.signUpContainer {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255, 255, 255);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999999;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.signUpWrapper {
    border-radius: 20px;
    width: 45vw;
    height: 70vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, 
                rgba(0, 0, 0, 0.12) 0px -12px 30px, 
                rgba(0, 0, 0, 0.12) 0px 4px 6px, 
                rgba(0, 0, 0, 0.17) 0px 12px 13px, 
                rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.signUpLeftSide {
    flex: 1;
    width: 50%;
    height: 100%;
}

.leftImg {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.signUpRightSide {
    margin: 0;
    flex: 1;
    background-color: #fff;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.signUpRightSide > h2 {
    text-align: center;
    font-size: 36px;
    color: #f22a2a;
    margin: 2.5rem 0;
}

.signUpRightSide > .signUpForm {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.signUpForm {
    margin-top: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 32px;
}

.signUpForm > input {
    outline: none;
    border: none;
    border-bottom: 1px solid #333;
    width: 50%;
    padding-left: 0.25rem;
    padding-bottom: 0.25rem;
    transition: 150ms all ease-in-out;
}

.signUpForm > input:focus {
    padding-left: 0.75rem;
}

.signUpForm > .submitBtn {
    margin-top: 2.5rem;
    outline: none;
    border: 1px solid transparent;
    padding: 0.5rem 1.5rem;
    background-color: #f22a2a;
    color: #fff;
    font-weight: 500;
    font-size: 20px;
    border-radius: 20px;
    cursor: pointer;
}

.signUpForm > .submitBtn:hover {
    border-color: #f22a2a;
    color: #f22a2a;
    background-color: #fff;
}

.signUpForm > p {
    text-align: center;
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 50px;
}

.signUpForm > p > a {
    text-decoration: none;
    font-size: 17px;
    color: #888;
    margin-top: 0.25rem;
}

.errorMessage {
    height: 70px;
    width: 250px;
    text-align: center;
    border-radius: 20px;
    line-height: 1.5rem;
    padding: 0.75rem 1.5rem;
    background-color: #000;
    color: #fff;
    position: absolute;
    top: 5rem;
    right: 5rem;
    font-weight: 500;
}

/* Responsive for phone */
@media only screen and (max-width: 600px) {
    .signUpWrapper {
        width: 90vw;
        height: auto;
        flex-direction: column;
    }

    .signUpLeftSide,
    .signUpRightSide {
        width: 100%;
        height: auto ;
    }

    .signUpForm > input {
        width: 80%;
    }

    
}
