/* General reset and font styling */
body, html {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, sans-serif;
  }
  
  .navcontainer {
    width: 100%;
    background-color: #343a40;
    padding: 1rem 0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    margin: 0 auto;
    max-width: 1200px;
  }
  
  .left .title {
    color: #fff;
    font-size: 1.5rem;
    text-decoration: none;
    font-weight: bold;
  }
  
  .right {
    display: flex;
    align-items: center;
  }
  .right .dashboardIcon,
  .right .add,
  .right .homeIcon,
  .right .logoutIcon {
    color: #fff;
    font-size: 1.5rem;
    margin-left: 1.5rem;
    cursor: pointer;
    transition: color 0.3s ease;
  }
  .right .dashboardIcon:hover,
  .right .add:hover,
  .right .homeIcon:hover,
  .right .logoutIcon:hover {
    color: #ff6347;
  }
  

  @media only screen and (max-width: 600px) {

    .navcontainer {
      width: 100%;
      background-color: #343a40;
      padding: 1rem 0;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    }
    
    .right .dashboardIcon,
  .right .add,
  .right .homeIcon,
  .right .logoutIcon {
    font-size: 1.3rem;
  }

  }