.addFormContainer {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
}

.addFormContainer .left,
.addFormContainer .right {
  width: 50%;
  height: 93vh;
}

.addFormContainer .left {
  display: flex;
  justify-content: center;
  align-items: center;
}

.addFormContainer .right {
}

.addWrapper {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  padding: 25px;
  display: flex;
  flex-direction: column;
  background-color: aliceblue;
  border-radius: 10px;
  width: 50%;
  height: auto;
  margin: 0 auto;
}
.addWrapper textarea,
.addWrapper select,
.addWrapper input {
  padding: 10px;
}

.addWrapper label {
  font-size: 1rem;
  margin-top: 10px;
}

/* Responsive for phone */
@media only screen and (max-width: 600px) {
    .addFormContainer {
        flex-direction: column;
    }

    .addFormContainer .left,
    .addFormContainer .right {
        width: 100%;
        height: auto;
    }

    .addWrapper {
        width: 90%;
        margin-top: 50px;
    }
}